import Page from 'components/Page';
import React,{useState} from 'react';
import {
  Button,
  Card,
  CardBody,
  CardHeader,
  Col,
  Form,
  FormFeedback,
  FormGroup,
  FormText,
  Input,
  Label,
  Row,
} from 'reactstrap';
import {
  FaSignOutAlt,
  FaUserEdit
} from 'react-icons/fa';
import {

  MdWarning
} from 'react-icons/md';
import {Link} from 'react-router-dom';

const MyPage = () => {
  const [password,setPassword]=useState('');
  const [confirmPassword,setConfirmPassword]=useState('');
  const submitPasword=()=>{
    if(password===confirmPassword){
      fetch("/api/change-password", {
        "method": "POST",
        "headers": {
          "content-type": "application/json",
          "Authorization":JSON.parse(localStorage.getItem('auth')).userToken

        },
        "body": JSON.stringify({
          password: password
        })
      })
        .then(response =>{ 
          if(response.status<400){
           
            alert('Success')
    
          }else{
            response.json().then(res=>{
              alert('refused');
            });
           
          }
    
      });
    }

  };
  return (
    <Page title="My Account"  >
      <Row>       
        
        <Col xl={12} lg={12} md={12}>
        <Link className={'form-control'}  color="danger" to="/logout" style={{'textAlign':'center'}} ><FaSignOutAlt /> LogOut</Link>
        {
          JSON.parse(localStorage.getItem('auth')).user.admin ? (
              <Link className={'form-control'}  color="danger" to="/user-admin" style={{'textAlign':'center'}} ><FaUserEdit /> Admin</Link>
          ) : ""
        }
          <Card>
            <CardHeader>Change Password</CardHeader>
            <CardBody>
              <Form>                

                <FormGroup>
                  <Label for="examplePassword">Password</Label>
                  <Input
                    type="password"
                    name="password"
                    value={password}
                    onChange={(e)=>{setPassword(e.target.value);}}
                    placeholder="password"
                  />
                </FormGroup>
                <FormGroup>
                  <Label for="examplePassword"> Confirm Password</Label>
                  <Input
                    type="password"
                    name="confirm_password"
                    value={confirmPassword}
                    onChange={(e)=>{setConfirmPassword(e.target.value)}}
                    placeholder="Confirm password"
                  />
                   {
                  password!==confirmPassword ? (
                    <span className='text-danger'>password not match</span>
                  ) : ''
                }
                </FormGroup>
                <Button onClick={submitPasword} color="primary" > ok</Button>
              </Form>
            </CardBody>
          </Card>
        </Col>
     
        
      </Row>
    </Page>
  );
};

export default MyPage;
